body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.cards {
    background: #fff;
    border-radius: 2px;
    height: 200px;
    margin: 1em;
    position: relative;
    float: left;
    width: 200px;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    text-align: left;
}

.cards > .img-container {
    height: 100%;
    overflow: hidden;
    text-align: center;
    background: rgb(0, 0, 0);
}

.cards > .img container > img {
    width: 100;
}

.cards > .content {
    padding-left: 1rem;
    padding-right: 1rem;
    font-size: 15px;
}
